<template>
  <v-content>
    <v-img src="@/assets/img/1.png" class="bdBackground" height="100%" width="100%"></v-img>
    <v-container>
      <v-row>
        <v-col>
          <v-card>
            <v-card-title>我的商品</v-card-title>
            <v-data-table
              show-select
              search
              :headers="table.headers"
              :items="table.desserts"
              :items-per-page="5"
              item-key="good_id"
              v-model="selected"
              class="elevation-12"
              no-data-text="你还没有发布商品"
            >
              <template v-slot:item.operation="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
              </template>

              <template v-slot:body.append>
                <tr>
                  <td colspan="4"></td>
                  <v-btn color="error" class="ma-2" @click="delGoods">批量删除</v-btn>
                </tr>
              </template>
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
      <v-dialog v-model="dialog.delDialog" persistent max-width="290">
        <v-card>
          <v-card-title class="headline">确认删除</v-card-title>
          <v-card-text>你确定要删除咩</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn color="green darken-1" text @click="dialog.delDialog = false">取消</v-btn>
            <v-btn color="error darken-1" text @click="deleteItemOk">删除</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialog.editDialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">修改商品</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-form v-model="valid">
                <v-text-field
                  v-model="operateItem.good_name"
                  :counter="20"
                  :rules="[rules.required,rules.notSpecial]"
                  label="商品名称"
                  prepend-icon="mdi-account"
                  required
                ></v-text-field>
                <v-textarea
                  v-model="operateItem.good_desc"
                  label="商品描述"
                  prepend-icon="mdi-account"
                  :rules="[rules.required]"
                ></v-textarea>
                <v-text-field
                  v-model="operateItem.good_price"
                  :rules="[rules.required,rules.numical]"
                  label="商品价格"
                  prepend-icon="mdi-account"
                  required
                  prefix="￥"
                ></v-text-field>
                <v-input prepend-icon="mdi-account" hide-details>
                  <cas
                    :data="categories"
                    v-model="casValue"
                    size="large"
                    placeholder="商品分类"
                    @on-change="casChange"
                  />
                </v-input>
                <v-file-input
                  multiple
                  label="商品图片"
                  prepend-icon="mdi-camera"
                  v-model="imgFile"
                  counter
                  chips
                  show-size
                  :rules="[rules.imgTypeAndSize]"
                ></v-file-input>
              </v-form>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn color="blue darken-1" text @click="dialog.editDialog = false">取消</v-btn>
            <v-btn color="blue darken-1" text @click="editItemOk">更新</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-snackbar bottom v-model="tip.status" :color="tip.color" :timeout="tip.timeout">{{tip.msg}}</v-snackbar>
    </v-container>
  </v-content>
</template>
<script>
import $qs from "qs";
import { log } from "util";
export default {
  name: "myRelease",
  created() {
    this.user = $qs.parse(sessionStorage.getItem("loginInfo"));
  },
  mounted() {},
  methods: {
    delGoods() {
      let _this = this;
      let selectedData = [];
      for (let i = 0; i < this.selected.length; i++) {
        selectedData[i] = this.selected[i].good_id;
      }
      if (selectedData.length > 0) {
        this.$axios({
          method: "delete",
          url: "/good/list",
          data: selectedData
        })
          .then(result => {
            _this.tipShow("批量删除成功");
            for (let i = 0; i < _this.selected.length; i++) {
              _this.table.desserts.splice(
                _this.table.desserts.indexOf(_this.selected[i]),
                1
              );
            }
          })
          .catch(err => {
            _this.tipShow("批量删除失败", "error");
          });
      }
    },
    editItem(item) {
      this.loadTypeData();
      this.operateItem = $qs.parse($qs.stringify(item));
      this.casValue[0] = parseInt(this.operateItem.dict_item_code);
      this.loadImgData();
      this.operateIndex = this.table.desserts.indexOf(item);
      this.dialog.editDialog = true;
    },
    editItemOk() {
      if (this.casValue.length == 0) {
        this.tipShow("请先选择分类", "error");
        return;
      }
      if (this.imgFile == 0) {
        this.tipShow("请先选择分类", "error");
        return;
      }
      let _this = this;
      this.$axios({
        method: "put",
        url: "/good",
        data: _this.operateItem
      })
        .then(result => {
          _this.table.desserts[_this.operateIndex].good_name =
            _this.operateItem.good_name;
          _this.table.desserts[_this.operateIndex].good_price =
            _this.operateItem.good_price;
          _this.table.desserts[_this.operateIndex].good_modify_time =
            _this.operateItem.good_modify_time;
          _this.dialog.editDialog = false;
          _this.casValue = [];
          _this.tipShow("修改成功", "success");
        })
        .catch(err => {
          _this.casValue = [];
          _this.tipShow("修改失败", "error");
        });
    },
    deleteItem(item) {
      this.operateItem = item;
      this.dialog.delDialog = true;
    },
    deleteItemOk() {
      const index = this.table.desserts.indexOf(this.operateItem);
      let _this = this;
      this.$axios({
        method: "delete",
        url: "/good",
        data: {
          user_id: _this.user.sid,
          good_id: _this.operateItem.good_id
        }
      })
        .then(result => {
          _this.dialog.delDialog = false;
          _this.tipShow("删除成功", "success");
          _this.table.desserts.splice(index, 1);
        })
        .catch(err => {
          _this.tipShow("删除失败", "error");
        });
    },
    tipShow(msg, color) {
      this.tip.status = true;
      this.tip.msg = msg;
      this.tip.color = color ? color : "success";
    },
    validImgs() {
      if (this.imgFile.length == 0) {
        return "文件不能为空";
      }
      for (var i = 0; i < this.imgFile.length; i++) {
        if (!this.imgFile[i].type.startsWith("image")) {
          return "所有文件必须是PNG、BMP、JPG格式";
        }
        if (this.imgFile[i].size >= 1048576) {
          return "单张图片必须小于1M";
        }
      }
      return true;
    },
    casChange(value, selectedData) {
      this.operateItem.dict_item_code = value[value.length - 1];
    },
    loadTypeData() {
      let _this = this;
      this.$axios({
        method: "get",
        url: "/dict/goods"
      })
        .then(res => {
          _this.categories = res.data;
        })
        .catch(err => {});
    },
    loadImgData() {
      var imgUrls = this.operateItem.good_image_url.split("-");
      let _this = this;
      for (let i = 0; i < imgUrls.length; i++) {
        var img = new Image();
        img.crossOrigin = "budingcompus.cn";
        img.src = imgUrls[i];
        let canvas = _this.ImageToCanvas(img);
        // canvas.toBlob(blob => {
        //   _this.imgFile[i] = blob;
        // });
        let dataurl = canvas.toDataURL("image/jpeg");
        console.log(dataurl);
      }
    },
    ImageToCanvas(image) {
      var canvas = document.createElement("canvas");
      canvas.width = image.width;
      canvas.height = image.height;
      canvas
        .getContext("2d")
        .drawImage(image, 0, 0, canvas.width, canvas.height); //0, 0参数画布上的坐标点，图片将会拷贝到这个地方
      return canvas;
    }
  },
  data() {
    return {
      operateItem: {},
      operateIndex: "",
      dialog: {
        delDialog: false,
        editDialog: false
      },

      valid: "",
      rules: {
        required: value => !!value || "不能为空",
        imgTypeAndSize: v => this.validImgs(),
        notSpecial: v =>
          /^[a-zA-Z0-9\u4e00-\u9fa5]+$/.test(v) || "只能包含字母、数字或中文",
        numical: v => /^[0-9]{1}[0-9]*[.]?[0-9]*$/.test(v) || "价格格式错误"
      },
      tip: {
        status: false,
        msg: "",
        color: "success",
        timeout: 2000
      },
      casValue: [],
      categories: [],
      imgFile: [],
      user: {},
      selected: [],
      table: {
        headers: [
          {
            text: "商品ID",
            value: "good_id"
          },
          { text: "名称", value: "good_name" },
          { text: "价格", value: "good_price" },
          { text: "操作", value: "operation" }
        ],
        desserts: []
      }
    };
  }
};
</script>